<script>
import { getTimeDuration } from '../../common/dateUtil';
import { prefix2Num } from '../../common/util';
import AnimationNumbers from '../AnimationNumbers';
const NOW_DATE = new Date();
// 下楼时间为 2019-01-05 04:30
const FIRST_DATE = new Date(2019, 0, 5, 4, 30);
const nowSinceFirstDate = NOW_DATE.getTime() - FIRST_DATE.getTime();

export default {
  name: 'DateCounter',
  components: {
    AnimationNumbers,
  },
  props: {},
  data() {
    return {
      sinceDate: nowSinceFirstDate,
      /** 是否展示女生的动效 */
      showGirlAni: false,
      /** 是否展示男生的动效 */
      showBoyAni: false,
    };
  },
  computed: {
    durationSinceDate() {
      const duration = getTimeDuration(this.sinceDate);
      return {
        days: prefix2Num(duration.days),
        hours: prefix2Num(duration.hours),
        mins: prefix2Num(duration.mins),
        secs: prefix2Num(duration.secs),
      };
    },
  },
  mounted() {
    setInterval(() => {
      this.sinceDate = this.sinceDate + 1000;
    }, 1000);
  },
  methods: {
    handleGirlClick() {
      // 如果正在动效中，不做处理
      if (this.showGirlAni) {
        return;
      }

      clearTimeout(this.girlAniRef);
      this.showGirlAni = true;
      this.girlAniRef = setTimeout(() => {
        this.showGirlAni = false;
      }, 3000);
    },

    handleBoyClick() {
      // 如果正在动效中，不做处理
      if (this.showBoyAni) {
        return;
      }

      clearTimeout(this.boyAniRef);
      this.showBoyAni = true;
      this.boyAniRef = setTimeout(() => {
        this.showBoyAni = false;
      }, 3000);
    },
  },
};
</script>
